.Graph {
    margin: 30px 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    height: 400px;
    width: 100%;
}


.GraphChart {
    /* width: 400px; */
    flex-grow: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: visible;

    background: repeating-linear-gradient(
    to right,
    #ddd,
    #ddd 1px,
    #fff 1px,
    #fff 9.988%
  );
}

.GraphVariables {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 0 20px 0 50px;
    min-width: 130px;
    text-align: center;
}

.GraphVariables span:nth-child(1) {
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
}
.GraphVariables span:nth-child(2) {
    -webkit-animation-delay: 550ms;
            animation-delay: 550ms;
}
.GraphVariables span:nth-child(3) {
    -webkit-animation-delay: 600ms;
            animation-delay: 600ms;
}
.GraphVariables span:nth-child(4) {
    -webkit-animation-delay: 650ms;
            animation-delay: 650ms;
}
.GraphVariables span:nth-child(5) {
    -webkit-animation-delay: 700ms;
            animation-delay: 700ms;
}
.GraphVariables span:nth-child(6) {
    -webkit-animation-delay: 750ms;
            animation-delay: 750ms;
}

.GraphVariable {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    -webkit-animation-name: appearVars;
            animation-name: appearVars;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes appearVars {
    from {opacity: 0;}
    to {opacity: 1; -webkit-transform: translateY(0); transform: translateY(0);}
}

@keyframes appearVars {
    from {opacity: 0;}
    to {opacity: 1; -webkit-transform: translateY(0); transform: translateY(0);}
}

.GraphChartItem {
    height: 20px;
    background: black;
    width: 0;
    
    -webkit-animation-duration: 500ms;
    
            animation-duration: 500ms;
    /* animation-delay: 800ms; */
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;

    transition: background .1s ease;
}
.GraphChartItem:hover {
    background: rgb(70, 70, 70);
    cursor: pointer;
}

.GraphOptions {
    margin: 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
    
    opacity: 0;
    -webkit-animation-name: appearOptions;
            animation-name: appearOptions;
    -webkit-animation-delay: 350ms;
            animation-delay: 350ms;
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.GraphOption {
    -webkit-writing-mode: vertical-lr;
            writing-mode: vertical-lr;
    text-orientation: sideways;
    font-size: .7em;
    font-weight: bold;
    color: darkgray;

    cursor: pointer;

    transition: all 100ms ease;
}

.VerticalLine {
    -webkit-writing-mode: vertical-lr;
            writing-mode: vertical-lr;
    text-orientation: sideways;
    font-size: .7em;
    font-weight: bold;
    color: darkgray;
}

.GraphOption:hover {
    -webkit-transform: scale(1.7);
            transform: scale(1.7);
}

.GraphOptionActive {
    -webkit-transform: scale(1.7);
            transform: scale(1.7);
    color: black;
}

.Percentage {
    opacity: 0;

    position: absolute;
    left: 100%;

    -webkit-animation-name: appearPerc;

            animation-name: appearPerc;
    -webkit-animation-delay: 1000ms;
            animation-delay: 1000ms;
    -webkit-animation-duration: 250ms;
            animation-duration: 250ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}


.Item60 {
    -webkit-animation-name: chartItem60;
            animation-name: chartItem60;
}

.Item70 {
    -webkit-animation-name: chartItem70;
            animation-name: chartItem70;
}

.Item80 {
    -webkit-animation-name: chartItem80;
            animation-name: chartItem80;
}

.Item90 {
    -webkit-animation-name: chartItem90;
            animation-name: chartItem90;
}

.Item100 {
    -webkit-animation-name: chartItem100;
            animation-name: chartItem100;
}

@media screen and (max-width: 768px) {
    .GraphChart {
        flex-grow: 1;
    }

    .GraphVariables {
        margin: 0 10px 0 20px;
        min-width: unset;
        /* max-width: 100px; */
    }
}

@-webkit-keyframes appearPerc {
    from {opacity: 0;}
    to {opacity: 1; -webkit-transform: translateX(0); transform: translateX(0) }
}

@keyframes appearPerc {
    from {opacity: 0;}
    to {opacity: 1; -webkit-transform: translateX(0); transform: translateX(0) }
}

@-webkit-keyframes appearOptions {
    0% {opacity: 0;}
    100% {opacity: 1; -webkit-transform: translateX(0); transform: translateX(0);}
}

@keyframes appearOptions {
    0% {opacity: 0;}
    100% {opacity: 1; -webkit-transform: translateX(0); transform: translateX(0);}
}

@-webkit-keyframes chartItem60 {
    from {width: 0;}
    to {width: 60%;}
}

@keyframes chartItem60 {
    from {width: 0;}
    to {width: 60%;}
}

@-webkit-keyframes chartItem70 {
    from {width: 0;}
    to {width: 70%;}
}

@keyframes chartItem70 {
    from {width: 0;}
    to {width: 70%;}
}

@-webkit-keyframes chartItem80 {
    from {width: 0;}
    to {width: 80%;}
}

@keyframes chartItem80 {
    from {width: 0;}
    to {width: 80%;}
}

@-webkit-keyframes chartItem90 {
    from {width: 0;}
    to {width: 90%;}
}

@keyframes chartItem90 {
    from {width: 0;}
    to {width: 90%;}
}

@-webkit-keyframes chartItem100 {
    from {width: 0;}
    to {width: 100%;}
}

@keyframes chartItem100 {
    from {width: 0;}
    to {width: 100%;}
}